.navbar {
    flex-grow: 1;
    font-size: larger;
    
    // .app-bar {
    //   background-color: transparent;
    //   backdrop-filter: blur(8px);
    //   transition: 0.3s ease-out;
    //   z-index: 1201;
    // }
  
    .toolbar {
      background-color: #fffcfc;
      display: flex;
      justify-content: space-between;
      padding: 16px 20px;
      height: 70px;
    }
  
    .logo {
      margin-top: 8%;
      margin-right: 20%;
      height: 80px;      width: auto;
      object-fit: contain;
    }
  
    .menu-items {
      display: flex;
      gap: 20px;
    }
  
    .user-section {
      display: flex;
      align-items: center;
    }
  
    .user-greeting {
      margin-left: 10px;
    }
    
    .mobile-menu-button {
      display: flex;
      align-items: center;
    }

    @media (max-width: 600px) {
      .logo {
        margin-top: 15%;
        margin-right: 0;
        height: 70px;
      }
    }

  }
  