.hebrew-date {
    position: relative;
    margin-left: 23px;
    left: 1px;
    top: 0px;
}

.hebrew-date-short {
    position: relative;
    margin-left: 36px;
    left: 1px;
    top: 0px;
}

.hebrew-month-year {
    display: inline-block;
}

h1{
    text-align: center;
    position: relative;
    top: 6rem;
}

@media (max-width: 425px) {

    .fc, .fc *, .fc ::after, .fc ::before {
        font-size: 14px;
    }

    .hebrew-date {
        margin-left: 4px;
        font-size: 14px; 
    }

    .hebrew-date-short {
        margin-left: 15px;
        font-size: 14px;   
     }

    .hebrew-month-year {
        display: block;
        text-align: center;
    }

    .hebrew-month-year .separator {
        display: none;
    }

}



.selected-day {
    background-color: #98947a;
}