.home-page {
  position: relative;
  text-align: center;
  font-size: larger;

  .background-image {
    width: 100%;
    object-fit: cover;
    object-position: center;
  }

  .appointment-button {
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 7px 80px;
    font-size: 20px;
    text-decoration: none;
    color: rgb(61, 50, 50);
    border: 1px solid rgb(61, 50, 50);
  }

  .about-section {
    padding: 2rem;
    background-color: #f5f5f5;
    text-align: center;
  }

  @media (max-width: 600px) {
    .appointment-button {
      padding: 0 2%;
      font-size: 16px;
    }
  }
}